import {
    Component,
    h,
    Prop,
    Element,
    Host,
    Event,
    EventEmitter,
} from '@stencil/core';
import { classlist } from '../../../utils/classlist';

const variantsConfig = {
    variants: {
        disabled: {
            false: '',
            true: 'bg-disabled-light pointer-events-none opacity-50 grayscale-[1]',
        },
        readonly: {
            false: '',
            true: 'pointer-events-none',
        },
        checked: {
            false: 'hover:bg-surface-1 bg-white',
            true: 'bg-active-foreground/15',
        },
    },
};

const getRadioClasses = classlist(
    'focus-visible:ring-active-foreground/30 relative flex w-full cursor-pointer items-start gap-8 rounded-md p-8 outline-0 transition-[color,background-color,box-shadow] focus:outline-none focus-visible:ring-2 focus-visible:ring-offset-2',
    variantsConfig,
);

/**
 * @category Form Controls
 * @description A radio button component for selecting a single option from a predefined list.
 *
 * @slot - Placed below the title/subtitle
 */
@Component({
    tag: 'at-radio',
    shadow: false,
})
export class AtRadio {
    @Element() el: HTMLElement;

    /**
     * Title of the radio component.
     */
    @Prop()
    label?: string;

    /**
     * Subtitle of the radio component.
     */
    @Prop()
    hint_text?: string;

    /**
     * Sets the value of the checkbox
     */
    @Prop({ mutable: true })
    value?: boolean;

    /**
     * ID of the radio button element and its option
     */
    @Prop()
    option_id?: string;

    /**
     * Name of radio group.
     * Buttons sharing a name are associated with the same radio group.
     */
    @Prop()
    group?: string;

    /**
     * Disables interaction with the radio
     */
    @Prop()
    disabled?: boolean;

    /**
     * Disables the radio option but keeps the text readable. Used for global readonly form state.
     */
    @Prop()
    readonly: boolean = false;

    radioEl: HTMLInputElement;

    /**
     * Emits the current state of the radio when clicked.
     */
    @Event()
    atuiChange: EventEmitter<boolean>;

    handleChange(): void {
        if (!this.disabled && !this.readonly && !this.value) {
            this.value = true;
            this.atuiChange.emit(this.value);
        }
    }

    handleKeyDown(event: KeyboardEvent) {
        if (event.key === 'Enter' || event.key === ' ') {
            event.preventDefault();
            this.handleChange();
        }
    }

    render() {
        const classname = getRadioClasses({
            disabled: this.disabled,
            checked: this.value,
            readonly: this.readonly,
        });

        return (
            <Host
                aria-checked={this.value}
                aria-disabled={this.disabled}
                aria-readonly={this.readonly}
                role="radio"
                tabindex={this.readonly ? -1 : 0}
                class={classname}
                data-name="radio-container"
                onKeyDown={(event) => this.handleKeyDown(event)}
                onClick={() => this.handleChange()}
            >
                {!this.readonly && (
                    <input
                        type="radio"
                        data-name="radio-input"
                        tabindex={-1}
                        name={this.group && this.group}
                        checked={this.value}
                        value={this.option_id}
                        id={this.option_id}
                        disabled={this.readonly || this.disabled}
                        ref={(el) => (this.radioEl = el)}
                    />
                )}

                <div class="pointer-events-none flex flex-col">
                    {this.label && (
                        <span
                            class="mt-0 pl-4 text-xs font-medium"
                            id={`${this.option_id}_label`}
                            data-name="radio-label"
                        >
                            {this.label}
                        </span>
                    )}
                    {this.hint_text && (
                        <span
                            class="text-light mt-0 pl-4 text-xs"
                            data-name="radio-hint"
                        >
                            {this.hint_text}
                        </span>
                    )}
                    <slot />
                </div>
            </Host>
        );
    }
}
